import React, { Component } from "react";

class BrandTwo extends Component{
    render(){
        return(
            <React.Fragment>
                <div class="section-title">
                <h2>Nossos valores</h2>
                <p>Nossos principais valores so: 
                    Unio - pois somos todos por uma, Equilbrio - para consiliar nossa vida,
                    nossa sade mental sem deixar nossa carreira de lado e 
                    Diversidade afinal o esteretipo no nos define.</p>
                </div>
                
                <ul className="brand-style-2">
                    <li>
                        <img src="/assets/images/brand/1.png" alt="Unio"/>
                    </li>
                    <li>
                        <img src="/assets/images/brand/2.png" alt="Equilbrio"/>
                    </li>
                    <li>
                        <img src="/assets/images/brand/3.png" alt="Diversidade"/>
                    </li>
                </ul>
            </React.Fragment>
        )
    }
}
export default BrandTwo;